<template>
  <div class="excel-table">
    <div
      class="excel-table-content"
      :class="[uid]"
      v-if="headers.length && columns.length"
    >
      <excel-table-header
        @selectCell="selectCell"
        @radioAuxiliaryLine="radioAuxiliaryLine"
      ></excel-table-header>
      <excel-table-column
        :height="height"
        @radioAuxiliaryLine="radioAuxiliaryLine"
        @selectAuxiliaryLine="selectAuxiliaryLine"
        ref="column"
      ></excel-table-column>
      <div class="excel-table-box" :class="[top]" :style="topBox"></div>
      <div class="excel-table-box" :class="[left]" :style="leftBox"></div>
      <div class="excel-table-box" :class="[right]" :style="rightBox"></div>
      <div class="excel-table-box" :class="[bottom]" :style="bottomBox"></div>
    </div>
  </div>
</template>

<script>
import ExcelTableHeader from './excel-table-header'
import ExcelTableColumn from './excel-table-column'

export default {
  name: 'excel-table',
  provide() {
    return {
      state: this.state,
      uid: this.uid,
      top: this.top,
      left: this.left,
      right: this.right,
      bottom: this.bottom
    }
  },
  props: {
    headers: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Array,
      default: () => []
    },
    height: {
      type: Number,
      default: 320
    }
  },
  data() {
    return {
      uid: this.guid(),
      top: this.guid(),
      left: this.guid(),
      right: this.guid(),
      bottom: this.guid(),
      state: {
        cellLeftWidth: 52,
        cellLeftHeight: 35,
        xIndex: [], // 是否选中表头
        yIndex: [], // 是否选中列
        headers: [], // 表头
        columns: [], // 列
        disabled: [], // 禁止列
        data: [] // 表头和列的一维数组
      }
    }
  },
  computed: {
    topBox() {
      return `top: 0;left: 0;width: 100%;height: ${this.state.cellLeftHeight}px;`
    },
    leftBox() {
      return `top: 0;left: 0;width: ${this.state.cellLeftWidth}px;height: 100%;`
    },
    rightBox() {
      return `top: 0;right: 0;width: ${this.state.cellLeftWidth}px;height: 100%;`
    },
    bottomBox() {
      return `bottom: 0;left: 0;width: 100%;height: ${this.state.cellLeftHeight}px;`
    }
  },
  methods: {
    radioAuxiliaryLine(x, y) {
      this.state.xIndex = [x]
      this.state.yIndex = [y]
    },
    selectAuxiliaryLine(x, y) {
      this.state.xIndex = [...new Set(x.sort((a, b) => a - b))]
      this.state.yIndex = [...new Set(y.sort((a, b) => a - b))]
    },
    selectCell(x, y, type) {
      this.$refs.column.selectCell(x, y, type)
    },
    getXIndex() {
      return this.state.xIndex
    },
    getYIndex() {
      return this.state.yIndex
    },
    setXIndex(val) {
      this.state.xIndex = val
    },
    setYIndex(val) {
      this.state.yIndex = val
    },
    guid() {
      return 'uxxxxxxxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        const r = Math.random() * 16 | 0
        const v = c === 'x' ? r : (r & 0x3 | 0x8)
        return v.toString(16)
      })
    }
  },
  watch: {
    headers: {
      handler() {
        this.state.headers = this.headers
      },
      immediate: true
    },
    columns: {
      handler() {
        this.state.columns = this.columns
      },
      immediate: true
    },
    data: {
      handler() {
        this.state.data = this.data
      },
      immediate: true
    },
    disabled: {
      handler() {
        this.state.disabled = this.disabled
      },
      immediate: true
    }
  },
  components: {
    [ExcelTableHeader.name]: ExcelTableHeader,
    [ExcelTableColumn.name]: ExcelTableColumn
  }
}

</script>

<style scoped lang="stylus">
