  position: relative;
  width: 100%;
  height: 100%;
  background-color: #fff;
  user-select: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;

  &::before, &::after {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    background-color: #ccc;
    content: ' ';
    z-index: 100;
  }

  &::before {
    width: 100%;
    height: 1px;
  }

  &::after {
    width: 1px;
    height: 100%;
  }

  .excel-table-content {
    width: 100%;
    height: 100%;
    overflow: auto;
  }

  & >>> table {
    width: 100%;
    text-align: left;
    border-collapse: separate;
    border-spacing: 0;
    box-sizing: border-box;
  }

  & >>> .excel-table-th {
    height: 34px;
    min-height: 34px;
    max-height: 34px;
    background-color: #f8f8f8;
  }

  & >>> .excel-table-td {
    height: 28px;
    min-height: 28px;
    max-height: 28px;
  }

  & >>> .excel-table-th, & >>> .excel-table-td {
    padding: 0;
    width: 52px;
    font-size: 12px;
    color: #333;
    border-bottom: 1px solid #ccc;
    border-right: 1px solid #ccc;
    text-align: center;
    box-sizing: border-box;
    text-overflow: ellipsis;
    vertical-align: middle;
    overflow hidden
  }

  & >>> .excel-table-th-fix-left {
    position: sticky;
