    z-index: 20;
    overflow: hidden;
    background-color: #f8f8f8;
  }

  & >>> .excel-table-hover {
    background-color: #FFF9EB;
  }
}

.excel-table-box {
  position: absolute;
  z-index: -1;
}
</style>
